// , mv, dj 
export const getUserPlaylist = (uid: number, limit = 30, offset = 0) => {
  return requestWithCK.get({
    url: '/user/playlist',
    params: {
      uid,
      limit,
      offset,
      timestamp: Date.now() //
    }
  })
}
