import React, { memo, useState } from 'react'
import type { ReactNode } from 'react'
import { LoginOtherWrapper } from './style'
import { message } from 'antd'
import { changeShowCodeMainAction, fetchCodeDataAction } from '../../store'
import { useAppDispatch } from '@/store'
interface IProps {
  children?: ReactNode
  showLoginMethod: (a: boolean) => void
}

const LoginOther: React.FC<IProps> = (props) => {
  const { showLoginMethod } = props
  const dispatch = useAppDispatch()

  const [isCheck, setCheck] = useState(false)

  function handleChangeMethod() {
    if (isCheck) {
      showLoginMethod(false)
      dispatch(fetchCodeDataAction())
    } else {
      message.open({
        content: '',
        key: 'check',
        duration: 1
      })
    }
  }

  function handleCheck() {
    setCheck(!isCheck)
  }

  function handleChangeToPhoneLogin() {
    if (isCheck) {
      dispatch(changeShowCodeMainAction(false))
    } else {
      message.open({
        content: '',
        key: 'check',
        duration: 1
      })
    }
  }

  return (
    <LoginOtherWrapper>
      <div className="content">
        <div className="top">
          <div className="left">
            <div className="img">
              <img src={require('@/assets/img/other_login_icon.png')} alt="" />
            </div>
            <div
              className="login_btn"
              onClick={(e) => handleChangeToPhoneLogin()}
            >
              /
            </div>
          </div>
          <div className="right">
            <ul>
              <li className="item">
                <a>
                  <i className="logo login_logo wx"></i>
                </a>
              </li>
              <li className="item">
                <a>
                  <i className="logo login_logo qq"></i>QQ
                </a>
              </li>
              <li className="item">
                <a>
                  <i className="logo login_logo wb"></i>
                </a>
              </li>
              <li className="item">
                <a>
                  <i className="logo login_logo wy"></i>
                </a>
              </li>
            </ul>
          </div>
        </div>
        <div className="bottom">
          <input
            type="checkbox"
            className="check"
            onChange={(e) => handleCheck()}
          ></input>
          
        </div>
        <div className="toCode" onClick={(e) => handleChangeMethod()}>
          <img
            className="code_img"
            src={require('@/assets/img/code_icon.png')}
            alt=""
          />
        </div>
      </div>
    </LoginOtherWrapper>
  )
}

export default memo(LoginOther)
