import myRequest, { requestWithCK } from '../../../service'

//
export const getPlaylistComments = (id: number, offset?: number) => {
  return myRequest.get({
    url: `/comment/playlist`,
    params: {
      id,
      offset,
      limit: 20,
      timestamp: Date.now()
    }
  })
}

//
export const operateComment = (
  t: number,
  type: number,
  id: number,
  content?: string,
  commentId?: number
) => {
  return requestWithCK.get({
    url: '/comment',
    params: {
      t, //1:2,0
      type, //2
      content,
      id, //id
      commentId
      // timestamp: Date.now()
    }
  })
}
