//

import myRequest, { requestWithCK } from '..'

//
export const newPlaylist = (name: string, privacy?: string, type?: string) => {
  return requestWithCK.get({
    url: '/playlist/create',
    params: {
      name,
      privacy,
      type
    }
  })
}

//
export const addSongTolist = (op: string, pid: number, tracks: number) => {
  return requestWithCK.get({
    url: '/playlist/tracks',
    params: {
      op,
      pid,
      tracks
    }
  })
}
